@import "../base/variables";
@import "../base/mixins";

.m-select-wrapper {
  cursor: pointer;
  position: relative;
  border: 1px solid $light-gray;
  padding: 12px;
  min-width: 200px;

  .selected {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .svg-inline--fa {
      color: $light-gray;
      transform-origin: 50% 50%;
      transition: all 0.2s;
      margin-left: 10px;
    }
  }

  .select-expand {
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: -1px;
    width: 100%;
    border: 1px solid $light-gray;
    transition: all 0.2s;
    transform: scale(0.75) translateY(-20%);
    opacity: 0;
    z-index: -1;
    box-sizing: content-box;

    .select-q {
      display: block;
      width: 90%;
      margin: 10px auto 10px auto;
      padding: 8px 12px;
      border-color: $light-gray;
    }

    .select-list {
      max-height: 200px;
      overflow-y: auto;
      @include scrollbar;
      li {
        padding: 12px;

        &:hover {
          background-color: rgba(0, 0, 0, 0.1);
        }
      }
    }
  }

  .m-select {
    display: none;
  }

  &.open {
    border-color: $color-primary;
    .select-expand {
      transform: scale(1) translateY(0);
      opacity: 1;
      z-index: 10;
    }

    .selected .svg-inline--fa {
      transform: rotate(180deg);
    }
  }
}
