@import "../base/variables";

.mp-info {
  background-color: $color-link;
  color: #fff;
  border-radius: 5px;
  width: 100%;
  display: flex;

  .mp-info-body {
    padding: 1.4rem;
    align-self: center;
  }

  .mp-info-header {
    padding: 1.4rem;
    text-align: center;
    border-top-left-radius: inherit;
    border-bottom-left-radius: inherit;
    background-color: darken($color: $color-link, $amount: 5);

    .svg-inline--fa {
      font-size: 1.6rem;
    }
  }

  &.warning {
    background-color: $color-warning;

    .mp-info-header {
      background-color: darken($color: $color-warning, $amount: 5);
    }
  }

  &.primary {
    background-color: $color-primary;

    .mp-info-header {
      background-color: darken($color: $color-primary, $amount: 5);
    }
  }

  &.danger {
    background-color: $color-danger;

    .mp-info-header {
      background-color: darken($color: $color-danger, $amount: 5);
    }
  }

  &.success {
    background-color: $color-success;

    .mp-info-header {
      background-color: darken($color: $color-success, $amount: 5);
    }
  }
}
