@import "../base/variables";

@mixin btn-color($bg-color, $color) {
  background-color: $bg-color;
  color: $color;

  &:hover {
    background-color: darken($color: $bg-color, $amount: 8);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
  }
}

.btn {
  border: none;
  padding: 12px 20px;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
  font-size: 100%;
  display: inline-block;

  &.small {
    font-size: 80%;
    padding: 6px 10px;
  }

  &.btn-primary {
    @include btn-color($color-primary, #fff);
  }

  &.btn-success {
    @include btn-color($color-success, #fff);
  }

  &.btn-warning {
    @include btn-color($color-warning, #fff);
  }

  &.btn-danger {
    @include btn-color($color-danger, #fff);
  }

  &.btn-white {
    @include btn-color(#fff, $color-primary);
  }
}
