@import "../base/variables";

.input-checkbox {
  cursor: pointer;
  display: flex;
  align-items: center;

  input {
    display: none;

    &:checked + .checkbox {
      background-color: $color-primary;
      border-color: transparent;
      &::before {
        display: block;
      }
    }
  }

  .checkbox {
    display: inline-block;
    width: 18px;
    height: 18px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin-right: 5px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform-origin: 50% 50%;
      transform: translate(-50%, -65%) rotate(-45deg);
      width: 7px;
      height: 3px;
      border-left: 2px solid #fff;
      border-bottom: 2px solid #fff;
      display: none;
    }
  }
}
