@import "../base/variables";
@import "../base/mixins";

.m-file-upload {
  cursor: pointer;
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $color-gray;
  border: 1px solid $light-gray;
  transition: all 0.4s ease-in-out;
  position: relative;
  font-size: 90%;
  text-align: center;

  &:hover,
  &.dragover {
    background-color: $lighter-gray;
  }

  input {
    visibility: hidden;
    position: absolute;
  }

  .svg-inline--fa {
    font-size: 250%;
    margin-bottom: 10px;
  }

  .initial-notifier {
    span {
      text-decoration: underline;
      font-weight: bold;
    }
  }

  .extension-notifier {
    position: absolute;
    bottom: -2px;
    left: 0;
    transform: translateY(100%);
    font-size: 78%;
    color: $color-gray;
  }

  &.has-files {
    .initial-notifier {
      display: none;
    }

    & > ul {
      height: 100%;
    }
  }

  & > ul {
    width: 100%;
    height: 0;
    overflow-x: auto;
    display: flex;
    align-items: center;
    @include scrollbar;

    .file {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      margin-right: 15px;

      &:first-child {
        margin-left: 10px;
      }

      & > div {
        color: $color-gray;
        white-space: nowrap;
      }

      img {
        display: block;
        width: 80px;
      }
    }
  }
}
