@import "../base/variables";

.sliding-input {
  display: inline-block;
  width: 100%;
  margin: 10px 0;

  .input-wrapper {
    position: relative;

    &.input-prefix {
      .placeholder {
        left: 60px;
      }

      input {
        padding-left: 60px;
      }
    }

    &.error {
      input {
        border-color: red;
      }
    }

    &.input-underline {
      &.error input {
        border-bottom-color: red;
      }

      input {
        border-radius: 0;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
      }
    }

    &.focus {
      input {
        border-radius: 3px;
        border-color: $light-gray;
      }

      .placeholder {
        cursor: default;
        top: 0;
        background-color: white;
        font-size: 70%;
        left: 5px !important;
        padding: 0 5px;
      }
    }

    .prefix {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 50px;
      height: 50%;
      border-right: 1px solid $light-gray;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 90%;
      color: #808080;

      .svg-inline--fa {
        font-size: 120%;
        cursor: pointer;

        &.active {
          color: $color-primary;
        }
      }
    }
  }

  input {
    width: 100%;
    padding: 12px 10px;
    border-width: 1px;
    border-style: solid;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: $light-gray;

    &.border {
      border-color: $light-gray;
      border-radius: 3px;

      & + .placeholder {
        left: 10px;
      }
    }

    &:focus {
      outline: none;
      border-color: $color-primary !important;

      & + .placeholder {
        color: $color-primary;
      }
    }
  }

  .placeholder {
    cursor: text;
    line-height: 1;
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    font-size: 90%;
    transition-property: top, left, font-size;
    transition-duration: 0.3s;
    color: $color-gray;
  }

  .error {
    color: red;
    font-size: 70%;
  }
}
