@import "../base/variables";

.input-radio {
  cursor: pointer;
  display: flex;
  align-items: center;
  span {
    display: inline-block;
    font-size: 90%;
  }

  .radio {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.1);
    position: relative;
    margin-right: 5px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 60%;
      height: 60%;
      background-color: $color-primary;
      border-radius: 50%;
      display: none;
    }
  }

  &.radio-box {
    .radio {
      display: none;
    }
    .text {
      padding: 8px 12px;
      background-color: rgba(0, 0, 0, 0.1);
    }

    input:checked + .text {
      background-color: $color-primary;
      color: #fff;
    }
  }

  input {
    display: none;

    &:checked + .radio::before {
      display: block;
    }
  }
}
