@import "../base/variables";
.panel-wrapper {
  display: flex;

  main {
    flex-grow: 1;
    width: 100%;
    padding-left: calc(2.4rem + 25px);

    @media (min-width: 1200px) {
      width: calc(100% - 270px);
      padding-left: 0;
    }
  }
}

.d-flex {
  display: flex;

  &.j-center {
    justify-content: center;
  }

  &.j-around {
    justify-content: space-around;
  }

  &.a-center {
    align-items: center;
  }

  &.j-between {
    justify-content: space-between;
  }

  &.j-end {
    justify-content: flex-end;
  }
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

$percents: (
  "25": 25%,
  "50": 50%,
  "75": 75%,
  "100": 100%,
);

@each $prop, $value in $percents {
  .w-#{$prop} {
    width: $value;
  }
}

@for $i from 1 through 5 {
  .mt-#{$i} {
    margin-top: #{0.6 * $i}rem;
  }
  .mb-#{$i} {
    margin-bottom: #{0.6 * $i}rem;
  }
  .ml-#{$i} {
    margin-left: #{0.6 * $i}rem;
  }
  .mr-#{$i} {
    margin-right: #{0.6 * $i}rem;
  }
  .mx-#{$i} {
    margin-right: #{0.6 * $i}rem;
    margin-left: #{0.6 * $i}rem;
  }
  .my-#{$i} {
    margin-top: #{0.6 * $i}rem;
    margin-bottom: #{0.6 * $i}rem;
  }

  .pt-#{$i} {
    padding-top: #{0.6 * $i}rem;
  }
  .pb-#{$i} {
    padding-bottom: #{0.6 * $i}rem;
  }
  .pl-#{$i} {
    padding-left: #{0.6 * $i}rem;
  }
  .pr-#{$i} {
    padding-right: #{0.6 * $i}rem;
  }
  .px-#{$i} {
    padding-right: #{0.6 * $i}rem;
    padding-left: #{0.6 * $i}rem;
  }
  .py-#{$i} {
    padding-top: #{0.6 * $i}rem;
    padding-bottom: #{0.6 * $i}rem;
  }

  .p-#{$i} {
    padding: #{0.6 * $i}rem;
  }

  .m-#{$i} {
    margin: #{0.6 * $i}rem;
  }
}

.text-center {
  text-align: center;
}

.text-small {
  font-size: 75%;
}

.shadow {
  box-shadow: 1px 2px 5px 0 rgba(0, 0, 0, 0.2);
}

.container {
  padding: 0 2%;
}

$colors: (
  "success": $color-success,
  "danger": $color-danger,
  "primary": $color-primary,
  "warning": $color-warning,
  "light": $light-gray,
  "gray": $color-gray,
  "white": #fff,
);

@each $prop, $value in $colors {
  .bg-#{$prop} {
    background-color: $value;
  }
  .text-#{$prop} {
    color: $value;
  }
}
