@mixin scrollbar($thumb-color: $light-gray) {
  &::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }
  &::-webkit-scrollbar-thumb {
    background: $thumb-color;
    border-radius: 5px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: darken($color: $thumb-color, $amount: 3);
  }
  &::-webkit-scrollbar-track {
    background: $lighter-gray;
    border-radius: 0px;
  }
}
