@import "../base/variables";

.mp-badge {
  cursor: default;
  display: inline-block;
  padding: 0.4rem 0.6rem;
  font-weight: lighter;
  font-size: 75%;
  border-radius: 5px;
  color: #fff;

  &.success {
    background-color: $color-success;
  }

  &.danger {
    background-color: $color-danger;
  }

  &.primary {
    background-color: $color-primary;
  }

  &.warning {
    background-color: $color-warning;
  }
}
