@import "../base/mixins";

.table-responsive {
  overflow-x: auto;
  @include scrollbar;
}

.table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
  text-align: left;

  &.table-bordered {
    th,
    td {
      border: 1px solid $light-gray;
    }
  }

  &.table-striped {
    tbody tr:nth-child(2n) {
      background-color: $lighter-gray;
    }
  }

  thead {
    tr {
      border-width: 4px;
    }

    th {
      padding: 12px;
    }
  }

  tr {
    border-bottom: 1px solid $lighter-gray;

    &:hover {
      background-color: $lighter-gray;
    }
  }

  th,
  td {
    white-space: nowrap;
    font-size: 90%;
  }

  td {
    padding: 20px 12px;
  }
}
