@import "./utils";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
}

html {
  font-size: 12px;

  @media (min-width: 576px) {
    font-size: 13px;
  }
  @media (min-width: 768px) {
    font-size: 14px;
  }
  @media (min-width: 992px) {
    font-size: 15px;
  }
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

input[type="text"],
input[type="email"],
input[type="phone"],
input[type="number"],
input[type="password"] {
  border: 1px solid $light-gray;
  border-radius: 3px;
  padding: 14px 12px;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}
