@import "../base/mixins";
@import "../base/variables";

.sidebar {
  background-color: #1e2129;
  width: 270px;
  max-width: 270px;
  height: 100vh;
  @include scrollbar(#8e8e90);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(calc(-100% + (2.4rem + 25px)));
  color: #fff;
  transition: all 0.6s;
  z-index: 1000;

  &.open {
    transform: translateX(0);
    overflow-y: auto;

    .expandable-name .svg-inline--fa {
      display: block !important;
    }
  }

  .nav-header {
    min-height: 150px;
    padding-left: 25px;

    h4 {
      font-weight: lighter;
      margin-top: 8px;
    }

    .user {
      margin-top: 5px;
      font-size: 95%;
    }
  }

  .nav-body {
    .list-header {
      padding-left: 25px;
      padding-bottom: 5px;
      color: rgba(255, 255, 255, 0.5);
      font-size: 70%;
    }

    & > ul {
      margin-bottom: 30px;

      & > li {
        & > a,
        .expandable-name {
          cursor: pointer;
          display: block;
          width: 100%;
          padding: 15px 15px 15px 25px;
          font-size: 90%;

          &:hover {
            background-color: lighten($color: #1e2129, $amount: 5);
          }
        }

        &.expandable {
          ul {
            background-color: #2d323e;
            height: 0;
            opacity: 0;
            visibility: hidden;
            transition: visibility 0.2s, opacity 0.2s, height 0.4s;

            a {
              display: block;
              width: 100%;
              padding: 15px 0 15px 35px;
              font-size: 75%;

              &:hover {
                background-color: lighten($color: #1e2129, $amount: 2);
              }
            }
          }

          .expandable-name {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .svg-inline--fa {
              font-size: 80%;
              transition: all 0.4s;
              display: none;
            }
          }

          &.expand {
            ul {
              opacity: 1;
              visibility: visible;
            }

            & > .expandable-name {
              background-color: lighten($color: #1e2129, $amount: 5);
              .svg-inline--fa {
                transform: rotate(180deg);
              }
            }
          }
        }
      }
    }
  }

  .sidebar-toggler {
    cursor: pointer;
    & > div {
      width: 25px;
      height: 2px;
      background-color: #fff;

      &:not(:first-child) {
        margin-top: 4px;
      }
    }
  }

  @media (min-width: 1200px) {
    position: sticky;
    transform: translateX(0);
    overflow-y: auto;

    .sidebar-toggler {
      display: none;
    }

    .expandable-name .svg-inline--fa {
      display: block !important;
    }
  }
}
